import { useState, useEffect } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';

const Home = () => {
  const [teamList, setTeamList] = useState([]);
  const history = useHistory();
  const getListTeam = async () => {
    try {
      const response = await axios.get('https://mlb-data.p.rapidapi.com/json/named.roster_40.bam?team_id=121', {
        headers: {
          "x-rapidapi-key": "dd7fc49cdamsh68c30fd2a7bbe51p13b26fjsn226a1e04928e",
          "x-rapidapi-host": "mlb-data.p.rapidapi.com",
          "useQueryString": true
        }
      });
      setTeamList(response?.data?.roster_40?.queryResults?.row)
    } catch (err) {
      console.log(err)
    }
  }

  useEffect(() => {
    getListTeam()
  }, [])

  return (
    <>
      <div style={{ fontSize: '40px', textAlign: 'center', width: '100%', marginBottom: '10px' }}>
       40-Man Roster MLB Team Player
    </div>
      <div style={{
        width: '700px',
        margin: 'auto',
        display: 'grid',
        fontSize: '24px',
        gridTemplateColumns: 'repeat(3, minmax(0, 1fr))',
        columnGap: '1.5rem',
        rowGap: '1.5rem',
      }}>
        {teamList.map((item, index) => {
          return (
            <div style={{
              backgroundColor: '#2cd459',
              padding: '4px 8px',
              borderRadius: '8px',
              color: 'white',
              cursor: 'pointer'
            }}
              onClick={() => { history.push(`/detail/${item?.player_id}`) }}
            >
              {item?.name_display_first_last}
            </div>
          )
        })}
      </div>
    </>)
}

export default Home;