import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';

const Detail = () => {
  const { id } = useParams();

  const [playerDetail, setPlayerDetail] = useState({});

  const getPalyerDetail = async () => {
    try {
      const response = await axios.get(`https://mlb-data.p.rapidapi.com/json/named.player_info.bam?sport_code=%27mlb%27&player_id=${id}`, {
        headers: {
          "x-rapidapi-key": "dd7fc49cdamsh68c30fd2a7bbe51p13b26fjsn226a1e04928e",
          "x-rapidapi-host": "mlb-data.p.rapidapi.com",
          "useQueryString": true
        }
      });
      setPlayerDetail(response?.data?.player_info?.queryResults?.row)
    } catch (err) {
      console.log(err)
    }
  }

  useEffect(() => {
    getPalyerDetail()
  }, [])

  console.log(playerDetail)
  const style = {
    row: {
      fontSize: '24px',
      display: 'flex',
      flexDirection: 'row',
      marginBottom: '6px'
    }
  }
  return (
    <>
      <div style={{ fontSize: '40px', textAlign: 'center', width: '100%', marginBottom: '10px' }}>
        Player Info
       </div>
      <div style={{
        display: 'flex', flexDirection: 'column', width: '700px',
        margin: 'auto', background: '#4287f5', padding: '20px'
      }}>
        <div style={style.row}>
          <div style={{ width: '40%', color: 'white' }}>
            Full Name
        </div>
          <div style={{ width: '60%', color: 'white' }}>
            {playerDetail?.name_full||'-'}
          </div>
        </div>
        <div style={style.row}>
          <div style={{ width: '40%', color: 'white' }}>
            Nick Name
        </div>
          <div style={{ width: '60%', color: 'white' }}>
            {playerDetail?.name_nick || '-'}
          </div>
        </div>
        <div style={style.row}>
          <div style={{ width: '40%', color: 'white' }}>
            Player ID
        </div>
          <div style={{ width: '60%', color: 'white' }}>
            {playerDetail?.player_id||'-'}
          </div>
        </div>
        <div style={style.row}>
          <div style={{ width: '40%', color: 'white' }}>
            Gender
        </div>
          <div style={{ width: '60%', color: 'white' }}>
            {playerDetail?.gender||'-'}
          </div>
        </div>
        <div style={style.row}>
          <div style={{ width: '40%', color: 'white' }}>
            Birth Date
        </div>
          <div style={{ width: '60%', color: 'white' }}>
            {playerDetail?.birth_date||'-'}
          </div>
        </div>
        <div style={style.row}>
          <div style={{ width: '40%', color: 'white' }}>
            Status
        </div>
          <div style={{ width: '60%', color: 'white' }}>
            {playerDetail?.status||'-'}
          </div>
        </div>
      </div>
    </>
  )
}

export default Detail;